'use strict';

/**
* Bradford distribution quantile function.
*
* @module @stdlib/stats-base-dists-bradford-quantile
*
* @example
* import quantile from '@stdlib/stats-base-dists-bradford-quantile';
*
* var y = quantile( 0.1, 0.1 );
* // returns ~0.096
*
* var y = quantile( 0.5, 5.0 );
* // returns ~0.290
*
* var myquantile = quantile.factory( 5.0 );
* y = myquantile( 0.4 );
* // returns ~0.210
*
* y = myquantile( 0.8 );
* // returns ~0.639
*
* y = myquantile( 1.0 );
* // returns 1.0
*/

// MODULES //

import setReadOnly from '@stdlib/utils-define-nonenumerable-read-only-property';
import main from './main.js';
import factory from './factory.js';


// MAIN //

setReadOnly( main, 'factory', factory );


// EXPORTS //

export default main;
